
body {
  margin: 0;
}
.transition, .modal__close:hover .svg-close {
  transition: -webkit-transform .25s ease-in-out;
  transition: transform .25s ease-in-out;
  transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.block_image {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 200px;
  width: 200px;
}
.cover {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.6);
  top: 0;
  left: 0;
}
.modal {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  width: 80vw;
  max-height: 90vh;
  background-color: white;
  border: 1px solid black;
  z-index: 999;
}
.modal__content {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.9);
}
.modal__close {
    position: absolute;
    top: 30px;
    right: 30px;
    z-index: 1000;
    border: 0;
    background: transparent;
}
.modal__close:focus {
      outline: none;
}
.modal__close:hover {
      cursor: pointer;
}
.modal__close:hover .svg-close {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
}
.modal__close .svg-close {
      width: 25px;
      height: 25px;
      transition: -webkit-transform .3s ease-in;
      transition: transform .3s ease-in;
      transition: transform .3s ease-in, -webkit-transform .3s ease-in;
}
.modal__close .svg-close__line {
        fill: white;
}
.gallery {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.gallery__image {
    max-height: 100%;
    max-width: 100%;
    -o-object-fit: contain;
       object-fit: contain;
}
.gallery__button {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.gallery__button_prev {
      left: 0;
}
.gallery__button_next {
      right: 0;
}
.page_hello {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
}
.page_hello h1, .page_hello h2 {
    font-weight: normal;
}
.page_hello ul {
    list-style-type: none;
    padding: 0;
}
.page_hello li {
    display: block;
    color: #42b983;
}
.page_hello li:hover {
      cursor: pointer;
}
.app-header {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid black;
  margin: 0;
  padding: 0;
}
.app-header__li {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    border: 1px solid blue;
}
.app-header__li:hover {
      cursor: pointer;
}
.app-header__li_logo {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 50%;
              flex: 0 0 50%;
}
.app-header__li_logo span {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}



